import Swiper, {Navigation, Pagination, EffectFade, Autoplay, Lazy} from "swiper"

Swiper.use([Navigation, Pagination, EffectFade, Autoplay, Lazy])

document.addEventListener("DOMContentLoaded", () => {
    window.initSliders()
})

window.initSliders = () => {

    const wideBanner = new Swiper('.wide-banner .swiper-container', {
        autoplay: {
            delay: 3000,
        },
        lazy: {
            loadPrevNext: true,
        }
    })


    const hotSwiper = new Swiper(".hot-news__slider", {
        effect: "fade",
        fadeEffect: {
            crossFade: true,
        },
        pagination: {
            clickable: true,
            el: ".hot-news__slider .swiper-pagination",
        },
    })

    const vectorSwiper = new Swiper(".vector__slider", {
        // loop: true,
        navigation: {
            prevEl: ".vector .swiper-button-prev",
            nextEl: ".vector .swiper-button-next",
        },
        effect: "coverflow",
        loopedSlides: 5,
        breakpoints: {
            0: {
                spaceBetween: 20,
                slidesPerView: "1",
                effect: "coverflow",
                coverflowEffect: {
                    rotate: 30,
                    slideShadows: false,
                    stretch: 10,
                    depth: 50,

                },
            },
            500: {
                slidesPerView: "auto",
                spaceBetween: 0,
                effect: "",
            },
        },
    })
}
