export default function initMobilemenu(params) {
    const burger = document.querySelector(".header__burger")
    if(!burger) return

    mobilemenuDropdown()

    const menu = document.querySelector('.mobilemenu')
    burger.addEventListener("click", () => {
        if (body.classList.contains("mobilemenu-opened")) {
            document.removeEventListener("click", outsideEvtListener)
        } else {
            setTimeout(() => {
                document.addEventListener("click", outsideEvtListener)
            })
        }

        burger.classList.toggle("opened")
        body.classList.toggle("mobilemenu-opened")
    })
    function outsideEvtListener(evt) {
        if (evt.target === menu || menu.contains(evt.target)) return
        burger.classList.toggle('opened')
        body.classList.toggle('mobilemenu-opened')

        document.removeEventListener('click', outsideEvtListener);
    }


    document.querySelector('.mobilemenu__burger').addEventListener('click', ()=>{
        burger.classList.toggle('opened')
        body.classList.toggle('mobilemenu-opened')
        document.removeEventListener('click', outsideEvtListener);
    })

    
    function mobilemenuDropdown(params) {
        const dropdownList = document.querySelectorAll(".mobilemenu .openable")
        if (dropdownList.length > 0) {
            document.addEventListener("click", (ev) => {
                if (ev.target.classList.contains("openable")) {
                    ev.target.classList.toggle("opened")
                } else {
                    dropdownList.forEach((item) => {
                        item.classList.remove("opened")
                    })
                }
            })
        }
    }

}
