export default function datepickerSplit(params) {
    const dates = document.querySelector("#news-datepicker")

    if (dates) {
        let from = document.querySelector("#news-from-date")
        let to = document.querySelector("#news-to-date")
        dates.addEventListener("blur", () => {
            const arr = dates.value.split(",")
            from.value = arr[0]
            to.value = arr[1]
        })
    }
}
